@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css");

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 100%;
  z-index: 1;
  background-color: black;
}

.slides .slick-prev {
  left: 5%;
}

.slides .slick-next {
  right: 5%;
}

#blogColumn_row {
  margin-left: 17%;
  overflow: hidden;
  padding: 36px 0px;
}

#blogColumn_row > div:first-child {
  max-width: 100%;
  margin: 0;
}

.blogColumn_grid {
  grid-column: 6/-2;
  display: flex;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  position: relative;
}

.blogColumn_container {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: repeat(6, 1fr);
}

.blogColumn_carousel::before {
  z-index: 1;
  content: "";
  position: absolute;
  left: -100px;
  top: -36px;
  height: calc(100% + 72px);
  opacity: 1;
}

.blogColumn_x:first-child {
  opacity: 1;
  transform: translateY(-24px);
}

.blogColumn_x {
  background-color: #ffffff;
  display: inline-block;
  float: left;
  border-radius: 20px 0px 20px 0px;
  box-shadow: 3px 3px 25px #b179b916;
  position: inherit;
}

.blogColumn_x img {
  border-radius: 20px 0px 0px 0px;
  object-fit: cover;
  object-position: 100% 0;
}

.blogColumn_x .blogExcerpt {
  margin: 32px;
}

.blogColumn_x p {
  color: #190c3f;
  font-family: proxima-nova, "Montserrat", sans-serif;
  font-size: 18px;
}

.blogColumn_x .blogTitle {
  color: #190c3f;
  font-family: "Archivo", "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: bold;
}

.blogRead {
  color: #8f6cb0;
  font-family: proxima-nova, "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  padding-bottom: 34px;
}

.blogRead a {
  color: #8f6cb0;
  font-family: proxima-nova, "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}

.blogPagination {
  color: #ffffff;
  display: inline-block;
  margin: 0 11% 0 19%;
  width: 70%;
  padding-top: 44px;
}

.blogPaginationL {
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  font-family: proxima-nova, "Montserrat", sans-serif;
  float: left;
  text-transform: uppercase;
  width: 29%;
}

.blogPaginationR {
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  font-family: proxima-nova, "Montserrat", sans-serif;
  float: right;
  width: 65%;
  text-transform: uppercase;
  text-align: right;
}

.blogPaginationR a {
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  font-family: proxima-nova, "Montserrat", sans-serif;
  float: right;
  width: 47%;
  text-transform: uppercase;
  text-align: right;
}

@media (min-width: 1024px) {
  .blogColumn_grid {
    grid-column: 7/-2;
  }
}

@media (min-width: 768px) {
  .blogColumn_container {
    grid-template-columns: repeat(24, 1fr);
  }
}
